@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #fff;
  background-color: #1a1a2e;
}

.app{
  background-color: #1a1a2e;
}

.favorite-list{
  background-color: black;
  border-radius: 20px;
  height: 150px;
  width: 1000px;
  margin-top: -35px;
  margin-left: 20px;
  overflow:scroll;
  overflow-x: hidden;
}

.all-coins {
  background-color: #1a1a2e;
}

.coin-app {
  background-color: #1a1a2e;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  color: #fff;
}

.coin-search {
  margin-bottom: 64px;
  display: block;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.coin-text {
  margin-bottom: 32px;
  font-size: 35px;
  text-align: center;
}

.coin-input {
  padding-left: 20px;
  width: 250px;
  height: 50px;
  border-radius: 8px;
  border: none;
  background-color: #bacacf;
  color: black;
}

.coin-input::-webkit-input-placeholder {
  color: black;
}

.coin-input:-ms-input-placeholder {
  color: black;
}

.coin-input::placeholder {
  color: black;
}

.navbar{
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.welcome{
  float: left;
  display: block;
  text-align: center;
  margin-top: 20px;
  margin-left: 20px;
  font-size: 22px;
  color: lightblue;
}

.log-button{
  float: right;
  display: inline;
  text-align: center;
  margin-top: 25px;
  margin-right: 20px;
  background-color: #2c8aa1;
  border-radius: 13px;
  border: none;
  color: white;
  padding: 9px 20px;
  text-decoration: none;
  font-size: 18px;
  cursor: pointer;
}

.git-logo {
  background-color: #2c8aa1;
  margin-right: 5px;
}

.txt-logo{
  font-size: 17px;
  background-color: #2c8aa1;
}

.fav-tag{
  margin-top: 20px;
  background-color: 1a1a2e;
  text-decoration: underline;
  -webkit-text-decoration: white;
          text-decoration: white;
}

.star-icon{
  float: left;
  margin-left: 946px;
  margin-bottom: 40px;
  margin-top: -50px;
  fill: yellow;
  background-color: #1a1a2e;
}

.coin-row {
  display: flex;
  flex-direction: row;
  justify-content: start;
  align-items: center;
  height: 80px;
  border-bottom: 1px solid #d7d7d7;
  width: 900px;
}

.fav-txt{
  color: white;
  background-color: black;
  margin-top: 45px;
  text-align: center;
}

*{
  background-color: #1a1a2e;
}

.coin-container {
    display: flex;
    justify-content: center;
  }
  
  .coin-row {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
  }
  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
  }
  
  .coin h1 {
    font-size: 19px;
    width: 150px;
  }
  
  .coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  
  .coin-symbol {
    text-transform: uppercase;
  }
  
  .coin-data {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
  }
  
  .coin-price {
    width: 110px;
  }
  
  .coin-volume {
    width: 155px;
  }
  
  .coin-marketcap {
    width: 230px;
  }
  
  .coin-percent {
    width: 100px;
  }

  .react-icon {
    width: 50px;
    cursor: pointer;
    fill: greenyellow;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }

  
